import Axios from "axios";
import { BusList, Fblogin } from "./actionType";

function dispatchers(type, dispatch,result) {
    return dispatch({
        type: type,
        payload: result.data
    });
}
export const Actions = {
    fblogin: authData => {
        console.log(authData)
        return dispatch => {
            Axios.post("http://cdt:8080/v1/api/add/fbuser", authData).then(res => {
                dispatchers(Fblogin,dispatch, res)
            });
        }
    },
    Sample: () => {
        return dispatch => {
            Axios.get("http://localhost:8080/v1/api/viewbuses").then(res => {
                dispatchers(BusList, dispatch,res)
            });
        };
    }
};
