import React from "react";
import FacebookLogin from "react-facebook-login";
import facebook from "../../assets/image/facebook.png";
import { Redirect } from "react-router-dom";
export default class FbLogin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      successLogin: false
    };
  }


  //check api and login
  facebookLogin = (response) => {
     this.props.fblogin(response)
      this.setState({successLogin:true})
  }


  render() {
    // let { children } = this.props;
    let { successLogin } = this.state;
    return (
      <div className="facebook">
        {successLogin ? <Redirect to="/bus-list" /> : null}
        <FacebookLogin
          appId="634105574031410"
          fields="name,email,picture"
          callback={this.facebookLogin}
          cssClass="facebook-icon"
          icon={<img src={facebook} />}
          textButton=""
        />
      </div>
    );
  }
}
