import React from 'react';
import logo from '../../assets/image/logo.png';
import FbLogin from "./fblogin";
import GmailLogin from "./gmaillogin";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { Actions } from '../../actions/actions';

class LoginComponent extends React.Component{
    constructor(props){
        super(props)
    }

    componentDidMount(){
        
    }
    getlogin = (err, result)=>{
        this.props.fblogin(result);
    }
    render(){
        return(
            <div className="col-lg-4">
            <div>
                <h6> <a className="navbar-brand" href="#"> <img src={logo} className="logo" /> </a> </h6>
            </div>
            <div className="sign_in_one">
                <h3>SIGN IN TO AVAIL EXCITING DISCOUNTS AND CASHBACKS!!</h3>
            </div>
            <div className="sign_in_two">
                <input className="input-1" type="number" placeholder="+91" />
                <input className="input-2" type="text" placeholder="Enter your mobile number" />
            </div>
            <div>
                <button className="button1">GENERATE OTP (One Time Password)</button>
            </div>
            <div className="after_button">
                <p> OR, Connect using social accounts</p>
            </div>
            <div className="social_accounts">
                <GmailLogin/>
                <FbLogin fblogin={this.getlogin}/>
            </div>
            <div className="after_social_accounts">
                <p> We will not post anything on your account</p>
            </div>
            <div className="after_social_accounts1">
                <p > By signing up, you agree to<br /> our <span>
                    <a href="#">Terms and Conditions</a>
                </span> and <span><a href="#" >Privacy Policy</a></span></p>
            </div>
        </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        fblogin: state.login
    };
};
const mapDispatchToState = dispatch => {
    return bindActionCreators(
        {
            fblogin: Actions.fblogin
        },
        dispatch
    );
};
export default connect(
    mapStateToProps,
    mapDispatchToState
)(LoginComponent);
