import React, { Fragment } from "react";
import Header from "../widgets/Header";
import { Redirect } from "react-router-dom";
import FilterComponent from "../widgets/filter";
import VRL from "../../assets/image/vrl-samll.png";
import SemiSleeper from "../widgets/Bus_seats/SemiSleeper";
class BookingPage extends React.Component {
  onProceed = () => {
    //   return  <Redirect to='/bus-list' />
    this.props.history.push("/bus-list");
  };
  render() {
    return (
      <div className="booking_search">
        <Header />
        <main>
          <div class="booking_page">
            <div class="booking_block_all">
              <div class="row">
                <FilterComponent />
                <div className="booking_right col-sm-9">
                  <h3 className="modify_title"> Modify Search </h3>
                  <div className="modify_form_block col-sm-12">
                    <div className="row form_row">
                      <div className="form_col col-sm-3">
                        <div className="form_group form-group">
                          <input
                            className="form-control bus_icon form_control"
                            type="text"
                            placeholder="Departure From"
                          />
                        </div>
                      </div>
                      <div className="form_col col-sm-3">
                        <div className="form-group form_group">
                          <input
                            className="form-control bus_icon form_control"
                            type="text"
                            placeholder="Arrival To"
                          />
                        </div>
                      </div>
                      <div className="form_col col-sm-3">
                        <div className="form-group form_group">
                          <input
                            className="form-control bus_icon form_control"
                            type="text"
                            placeholder="Today"
                          />
                        </div>
                      </div>
                      <div className="form_col col-sm-3">
                        <div className="form-group form_group">
                          <input
                            className="form-control bus_icon form_control"
                            type="text"
                            placeholder="Tomorrow"
                          />
                        </div>
                      </div>
                    </div>
                    <div className="row form_row">
                      <div className="form_col col-sm-6 padding_none">
                        <div className="form-group form_group col-sm-6">
                          <input
                            className="form-control bus_icon form_control"
                            type="text"
                            placeholder="Adult"
                          />
                        </div>
                        <div className="form-group form_group col-sm-6">
                          <input
                            className="form-control bus_icon form_control"
                            type="text"
                            placeholder="Children"
                          />
                        </div>
                      </div>
                      <div className="form_col col-sm-6">
                        <button
                          type="button"
                          name="Search"
                          className="submit-btn"
                        >
                          {" "}
                          Search{" "}
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className="search_result_list col-sm-12">
                    <div className="result_img">
                      <img src={VRL} alt="vrl" />
                    </div>
                    <div className="stop_cover">
                      <div className="result_from_date">
                        <span className="result_depart">Depart</span>
                        <p className="result_date">29/11/2019</p>
                        <h5 className="from_time">10:30 PM</h5>
                        <span className="from_place">Bangalore</span>
                      </div>
                      <div className="total_stops">
                        <div className="stop_before"></div>
                        <div className="stop_process">
                          <a href="#"> 0 Stops </a>
                          <label> 8 hours 40 mins</label>
                        </div>
                        <div className="stop_after"></div>
                      </div>
                      <div className="result_to_date">
                        <span className="result_arrive">Arrive</span>
                        <p className="to_date">29/11/2019</p>
                        <h5 className="to_time">10:30 PM</h5>
                        <span className="to_place">Hyderabad</span>
                      </div>
                    </div>
                    <div className="result_price_list">
                      <span className="price_title">Price</span>
                      <h4 className="list_price"> Rs.1200 </h4>
                      <span className="price_tax">+ 18% taxes</span>
                    </div>
                    <div className="modify_seat_available">
                      <span className="seat_count"> 9 </span>
                      <label className="seat_label"> seats available </label>
                    </div>
                    <div className="select_seat">
                      <div className="form_col col-sm-12 padding_none">
                        <button
                          type="button"
                          name="Search"
                          className="submit-btn"
                        >
                          {" "}
                          Select seats{" "}
                        </button>
                      </div>
                    </div>
                  </div>

                  <div className="booking_places col-sm-12">
                    <SemiSleeper />
                 
                  <div className="place_right col-sm-5">
                    <h3 className="onward_journey"> Onward Journey </h3>
                    <p> From bangalore To Hyderabad On 31 December, 2019 </p>
                    <div className="from_to_details">
                      <div className="seat_from_date">
                        <span className="seat_depart">Depart</span>
                        <p className="seat_date">29/11/2019</p>
                        <h5 className="seat_time">10:30 PM</h5>
                        <span className="from_place">Bangalore</span>
                      </div>
                      <div className="bus_from_to"></div>
                      <div className="seat_to_date">
                        <span className="seat_arrive">Arrive</span>
                        <p className="seat_date">29/11/2019</p>
                        <h5 className="seat_time">10:30 PM</h5>
                        <span className="to_place">Hyderabad</span>
                      </div>
                    </div>
                    <div className="boarding_to_dropping">
                      <div className="row form_row">
                        <div className="form_col col-sm-6">
                          <div className="form_group form-group">
                            <input
                              className="form-control bus_icon form_control"
                              type="text"
                              placeholder="Boarding Point"
                            />
                          </div>
                        </div>
                        <div className="form_col col-sm-6">
                          <div className="form-group form_group">
                            <input
                              className="form-control bus_icon form_control"
                              type="text"
                              placeholder="Dropping Point"
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="seat_total_price">
                      <div className="total_price_details">
                        <div className="price_row">
                          <label> Seats Selected </label>
                          <span> U19,U16 </span>
                        </div>
                        <div className="price_row">
                          <label> Basic Fare </label>
                          <span> 1780.00 </span>
                        </div>
                        <div className="price_row">
                          <label> GST </label>
                          <span> 89.00 </span>
                        </div>
                        <div className="price_row">
                          <label> Service Charge </label>
                          <span> 32.04 </span>
                        </div>
                        <div className="price_row">
                          <label> Amount </label>
                          <span> 1910.04 </span>
                        </div>
                      </div>
                      <div className="total_price_label">
                        <span> Price </span>
                      </div>
                      <div className="total_price_list">
                        <h4 className="total_price"> Rs.1910.04 </h4>
                      </div>
                    </div>
                    <button type="button" name="Proceed" className="submit-btn">
                      {" "}
                      Continue to make Payment{" "}
                    </button>
                  </div>
                  
				  </div>
				  <div className="footer_menus col-sm-12">
                    <ul className="menus_lists">
                      <li>
                        <a href="#">Amenities</a>
                      </li>
                      <li>
                        <a href="#">Bus Photos</a>
                      </li>
                      <li>
                        <a href="#">Boarding &amp; Dropping Points</a>
                      </li>
                      <li>
                        <a href="#">Rating &amp; Reviews</a>
                      </li>
                      <li>
                        <a href="#"> Booking policies </a>
                      </li>
                      <li>
                        <a href="#">
                          Rest Stop <span>(1)</span>
                        </a>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </main>
      </div>
    );
  }
}

export default BookingPage;
