import React from 'react';
import GoogleLogin from 'react-google-login';
import { Fragment } from 'react';
import google from '../../assets/image/google.png'
import { Redirect } from "react-router-dom";
export default class GmailLogin extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          successLogin: false
        };
      }  
      responseGoogle=(e)=>{
        console.log(e);
         this.setState({successLogin:true});
    } 
    render() {
        let { successLogin } = this.state;
        return (
            <Fragment>
          {successLogin ? <Redirect to="/bus-list" /> : null}
          <div className="google">
              
            <GoogleLogin
            
            clientId="594202953383-63h25nacvoe46jdv5m7hkap8o66eug68.apps.googleusercontent.com"
            onSuccess={this.responseGoogle}
            onFailure={this.responseGoogle}
            cookiePolicy={'single_host_origin'}
            >
                <img src={google} alt="google" className="google_icon"/> 
            </GoogleLogin>
            
            </div>
            </Fragment>
        )
    }
} 