import React from "react";
import { Link } from "react-router-dom";
import logo from "../../assets/image/logo.png";

class Header extends React.Component {
	render() {
		return (
			<header>
				<div className="header_bg">
					<div className="container-fluid">
						<nav className="navbar navbar-default">
							<div className="navbar-header">
								<button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
									<span className="sr-only">Toggle navigation</span>
									<span className="icon-bar"></span>
									<span className="icon-bar"></span>
									<span className="icon-bar"></span>
								</button>
								<h1> <Link className="navbar-brand" to="/"> <img src={logo} className="logo" /> </Link> </h1>
							</div>
							<div id="navbar" className="navbar-collapse collapse">
								<ul className="row join_login nav navbar-nav navbar-right">
									<li className="active"><a href="index.html">home</a></li>
									<li><a href="#">Bus</a></li>
									<li><a href="#">Taxi</a></li>
									<li>
										<a href="#" className="login_icon_all"> <span className="login_icon"> </span> Login  </a>
										<span className="login_border">  </span>
									</li>
								</ul>
							</div>
						</nav>
					</div>
				</div>
			</header>
		);
	}
}

export default Header;
